import React from 'react';
import { Row, Col} from 'reactstrap';
import Navbar from './Navbar';
import Footer from './footer';


function About() {
    return (
        <>  
        < Navbar />
        
     
 <div class="hero-image1">
  <div class="overlay"></div>
  <div class="hero-text">
    <br />
    <br />
    <h1>O nas</h1>
  </div>
</div> 
{/* <div className="hero-image">
  <div className="hero-text">
    
    <p style={{fontSize:'30px'}}>O nas</p>
  </div>
</div>   */}

         <section style={{padding:'0px', marginLeft:'0px',marginRight:'0px',}}> 

            <Row className="my-row">
                <Col xs={12} md={6}>
                    <p
                        style={{
                            fontSize: '1.5rem', color:'#244563'
                        }}>O nas</p>
                    <p>
                        Akademia BHP Mielczarek to firma specjalizujca si w dziedzinie bezpieczestwa
                        i higieny pracy, ktrej idea powstaa na podstawie wieloletniego dowiadczenia
                        zdobytego w prowadzeniu szkole BHP oraz pracy w midzynarodowych
                        przedsibiorstwach produkcyjnych o wysokich standardach bezpieczestwa. Firma ma
                        siedzib w Grze, wojewdztwo dolnolskie, oddalonej o 100 km od miast takich
                        jak Wrocaw, Pozna oraz Zielona Gra. Od 2020 roku firma posiada rwnie
                        oddzia w [nazwa miasta], co umoliwia wiadczenie usug na terenie caego
                        kraju.
                    </p><br/>
              
               <p> Akademia BHP Mielczarek oferuje szeroki zakres usug, w tym:</p>
               <ol>

                <li>Szkolenia z zakresu BHP: Firma zapewnia peen zakres szkole z zakresu
                bezpieczestwa i higieny pracy, aby pomc przedsibiorstwom i pracownikom
                przestrzega obowizujcych przepisw oraz minimalizowa ryzyko wypadkw i
                chorb zawodowych.</li>
               
             
              <li>
Obsuga przedsibiorstw: Akademia BHP Mielczarek oferuje sta obsug
                firm w zakresie BHP, pomagajc im w dostosowaniu si do regulacji i standardw
                bezpieczestwa.
              </li>
             
            
              <li>
Szkolenia operatorw wzkw jezdniowych: Firma prowadzi szkolenia dla
                operatorw wzkw jezdniowych, zapewniajc bezpieczn wymian butli gazowych i
                przestrzeganie wszystkich przepisw zwizanych z tymi maszynami.
              </li>
             <li>Szkolenia z pierwszej pomocy: Firma prowadzi szkolenia z pierwszej pomocy,
                pomagajc pracownikom zdoby niezbdne umiejtnoci w zakresie udzielania pomocy
                w nagych przypadkach.</li>
            </ol>
          

        </Col>
        <Col xs={12} md={6}>
           
           <br/>
           <br/>

            <p>
                Dodatkowo, firma zajmuje si kompleksowym zaopatrzeniem firm w artykuy BHP oraz
                sprzt przeciwpoarowy. Ich usugi s oferowane zgodnie z obowizujcymi
                przepisami i najwyszymi standardami, co sprawia, e speniaj oczekiwania
                najbardziej wymagajcych klientw.
            </p>
            <p>
                Akademia BHP Mielczarek angauje si rwnie w przegldy urzdze
                przeciwpoarowych, takich jak ganice i owietlenie ewakuacyjne, oraz oferuje
                pomiary natenia owietlenia. Te usugi s istotne dla utrzymania odpowiedniego
                poziomu bezpieczestwa przeciwpoarowego oraz zapewnienia waciwego
                owietlenia, co ma kluczowe znaczenie dla zdrowia i bezpieczestwa pracownikw.
            </p>
            <p>
                Kierujc swoj ofert do firm na terenie caego kraju, Akademia BHP Mielczarek
                stanowi solidnego partnera dla przedsibiorstw, ktre dbaj o bezpieczestwo
                swoich pracownikw, chc przestrzega obowizujcych przepisw i utrzymywa
                wysokie standardy bezpieczestwa w miejscu pracy. Dziki bogatemu dowiadczeniu
                i profesjonalnemu podejciu, firma pomaga swoim klientom w osigniciu tych
                celw.
            </p>
        </Col>
    </Row>

</section>  

<Footer/>
</>
    );
}

export default About;